import defaultIcons from "lib/icons";
import React from "react";
import IconD from "type/IconD";
import TagD from "type/TagD";
import { Icon } from "./Icon";
import styled from "styled-components";

const IconLi = styled.div`
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    padding-top: 10px;
    .cost.selected{        
            color: #1296db;        
    }
    .earn.selected{       
            color: red;        
    }
    .item{
        display: flex;
        flex-direction: column;
        align-items: center;
        padding-bottom: 10px;
        margin-bottom: 8px;
    }
`;

type Props = {
    type:'-'|'+';
    currenTag:TagD;
    onChange: (name:string) => void
};

const IconList:React.FC<Props> = (props) =>{
    const {currenTag, type} = props;
    const icons:IconD[] = defaultIcons.filter(icon=>icon.type===type);
    
    return (
        <IconLi>
            {icons.map(icon=>
                        <div key={icon.name}
                            className={
                               ['item',
                                icon.type==='-'?'cost': 'earn', 
                                currenTag.name===icon.name? 'selected':'']
                                .join(' ')} 
                            onClick={()=>props.onChange(icon.name)} >
                            <Icon name={icon.name} />
                        </div> )}
        </IconLi>
    )
};
export {IconList};