import styled from "styled-components"
import { Nav } from "./Nav"

const Wrapper = styled.div`
    display: flex;
    flex-direction: column;
    height: 100vh;
    .content{
        flex-grow: 1;
        overflow: auto;
        display: flex;
        flex-direction: column;
        &::-webkit-scrollbar{ 
            display:none;         
        }
    }
`;
const Layout:React.FC = (props) => {
    return (    
        <Wrapper>
            <div className='content'>
                {props.children}
            </div>
            <Nav /> 
        </Wrapper>
    )
}
export {Layout}