import React from "react";

type Props = {
    value: string;
    onChange:(text:string)=>void
};

const Input:React.FC<Props> = (props)=>{
    const {value, onChange} = props;
    return (
        <input type="text" 
                placeholder={value}
                value={value}
                onChange={(e)=> onChange(e.target.value) } />
    )
};
export {Input}