import { useState } from "react"
import styled from "styled-components"

const Wrapper = styled.ul`
    background-color: #d9d9d9;
    display: flex;
    text-align: center;
    font-size: 18px;
    > li{
        width: 50%;
        padding: 8px 0;
        display: flex;
        justify-content: center;
        align-items: center;
        position: relative;
        &.selected{
            color: white;
            background-color: #1296db;
        }
    }
`;
type Props = {
    value: '-' | '+'; 
    onChange: (selectedType: '-'|'+') => void 
}
const Types:React.FC<Props> = (props) =>{ 
    //React.FC<P> 指除了默认的React.FC的props类型，还有P类型
    const [typeList] = useState<('-' | '+')[]> (['-', '+']);
    const selectedType = props.value;
    const typeMap = { '-': '支出', '+': '收入'}   
    return (
        <Wrapper>
            {typeList.map(t=>
                <li className={selectedType===t ? 'selected' : ''} 
                    key={t}
                    onClick={()=>props.onChange(t)}>
                        {typeMap[t]}
                </li>
                )}
        </Wrapper>
    )
};

export {Types};