import styled from "styled-components";

 
const Wrapper = styled.div`
    text-align: center;    
    > .newTag-bt{
        margin: 8px;
        background-color: #1296db;
        color: white;
        border-radius: 4px;
        padding: 6px 16px;
        border: none;
    }
`;
type ButText = {
    butText:string;
    changeOk:() => void;
}
const Button:React.FC<ButText>= (props)=>{
    return (
        <Wrapper>
            <button className="newTag-bt" onClick={() => props.changeOk()}>{props.butText}</button>
        </Wrapper>
    )
};
export {Button};