import { Back } from "components/Back";
import { useTags } from "hooks/useTags";
import { useHistory, useParams } from "react-router-dom";
import styled from "styled-components";
import TagD from "type/TagD";
import clone from "lib/clone";
import { useState } from "react";
import { Button } from "components/Button";
import { IconList } from "components/IconList";
import { Input } from "components/Input";

type Params = {
    id: string
}
const Wrapper = styled.div`
    position: relative;
    height: 100vh;    
    input{
        margin-top: 10px;
        font-size: 22px;
        background-color: #f5f5f5;
        padding: 4px 16px;
        width:100%;
        text-align: left;
        border: none;
    }
    .tagArea{
        padding-top: 30px;
    }
`;

const EditTag:React.FC = (props) => { 

    const {currentTag, updateTag} = useTags();   
    const history = useHistory();
    // //从路径的得到id   
    let id = parseInt(useParams<Params>().id);
    // //根据id号得到 & 拷贝当前的标签   
    const curTag = clone(currentTag(id));   
    
    const [editTag, setEditTag] = useState<TagD>(curTag);
    const changeTag = (obj:Partial<TagD>) => {
        setEditTag({
            ...editTag,
            ...obj
        })
    };    
    const editOk = () => {
        updateTag(id, {name:editTag.name, tagContent:editTag.tagContent});
        history.goBack();
    };

    return (
        <Wrapper>
            <Back headText='编辑标签'/>
            <Input value={editTag.tagContent} onChange={(tagContent)=>changeTag({tagContent})} />
            <div className="tagArea">
                <IconList 
                    currenTag={editTag} 
                    type={editTag.type} 
                    onChange={name=>changeTag({name})} />
            </div>
            <Button butText="保存"
                changeOk={editOk}>
            </Button>
        </Wrapper>
    )
};
export {EditTag};