import { Button } from "components/Button";
import { Icon } from "components/Icon";
import { Layout } from "components/Layout";
import { Types } from "components/Types";
import { useTags } from "hooks/useTags";
import { useState } from "react";
import { Link, useHistory } from "react-router-dom";
import styled from "styled-components";

const TagArea = styled.div`
    max-height: 80%;
    overflow: auto;
    &::-webkit-scrollbar{ 
        display:none;         
    }
    a{     
        background-color: white;
        color: #333;
        border: 1px solid #d9d9d9;
        min-height: 44px;
        border-radius: 8px;       
        margin: 10px 12px;
        display: flex;
        align-items: center;
        > div{
            margin-left: 12px;
        }
        > span{
            padding: 0 10px;
            font-size: 16px;
            overflow: hidden; 
            white-space:nowrap; 
            text-overflow:ellipsis;
        }        
    }

`;

const Label = () => {
    const [labelType, setLabelType] = useState<'-' | '+'>('-');    
    const tagsType = useTags().tags.filter(tag=>tag.type===labelType);  
    const history = useHistory();  
    const path = `/create-tag/${labelType.toString()}`;
    return (
        <Layout>  
            <Types 
                value={labelType}
                onChange={type=>setLabelType(type)}/>
            <TagArea>
                {tagsType.map(tag=>
                    <Link to={'/tags/' + tag.id} key={tag.id} >
                        <div>
                        <Icon name={tag.name} />
                        </div>
                        <span>{tag.tagContent}</span>                        
                    </Link>)}               
            </TagArea>
            <Button 
                butText="新建标签" 
                changeOk={() => history.push(path)}/>            
        </Layout>
       
    )
};
export {Label};