import { Icon } from "components/Icon";
import { Layout } from "components/Layout"
import { Groups } from "components/Stats/Groups";
import { Types } from "components/Types";
import { useRecords } from "hooks/useRecords";
import { useTags } from "hooks/useTags";
import { shortDate } from "components/Stats/shortDate";
import { useState } from "react";
import { RecordItem } from "type/RecordItem";
import { groupList } from "components/Stats/GroupList";
import { StatsEcharts } from "components/Stats/StatsEcharts";
import styled from "styled-components";

const TitleText = styled.span`
    align-self: center;
    padding-top: 10px;
    font-weight: bolder;
`;
const Main = styled.div`
    overflow-y: auto;
    &::-webkit-scrollbar{ 
        display:none;         
    }
`;

const Statistics = () => {
    const [statsType, setStatsType] = useState<'-' | '+'>('-');
    const {records} = useRecords();
    const {currentTag} = useTags();
     //区分出 支出 和 收入 的记录
    const recordTypeList:RecordItem[] = records.filter(t => t.type === statsType );
    const groups = groupList(recordTypeList);     
    return (
        <div>
            <Layout>  
                <Types 
                    value = { statsType }
                    onChange = { type => setStatsType(type)} />
                <TitleText>历史记录</TitleText>
                <StatsEcharts type={statsType} groups={groups} />
                <Main>             
                <Groups>
                    {groups.map(groupD =>
                        <li className="group" key={groupD.title}>
                            <h3>{shortDate(groupD.title)} <span> ￥{groupD.total} </span></h3>
                            <ol className="items">
                                {groupD.items.map(item=>                               
                                    <li className="item" key={item.id}>
                                        <Icon name={currentTag(item.tagId).name}/>
                                        <div className="tagContent">
                                            {currentTag(item.tagId).tagContent} <br/>
                                            <span className="notes">{item.notes}</span>
                                        </div>
                                        <span className="amount">{item.amount} </span>
                                    </li>                                
                                    )}
                            </ol>
                        </li>
                        )}
                </Groups>   
                </Main>  
            </Layout>
        </div>
    )
};
export {Statistics};