// 接受 历史记录groups 生成 ECharts 图表的 option 部分
import dayjs from "dayjs";
import { EChartOption } from "echarts";
import _ from 'lodash';
import { StatsEcharts2 } from "./StatsEcharts2";
type Props = {
    groups: GroupsHashD[],
    type: '-' | '+'
};

const StatsEcharts = (props:Props) => {
    const {groups, type} = props;
    const arrayEchart = () => {
        const arrayChart:{date:string, total: number}[] = [];
        for(let i = 0; i < 30; i++){
            const dayString = dayjs().subtract(i, 'day').format("YYYY-MM-DD");            
            const findDay = _.find(groups, {
                title: dayString
            });
            let value2 = 0;
            if(findDay){value2 = findDay.total};
            arrayChart.push({
                date: dayString, total: value2
            })
        };
        arrayChart.sort((a, b)=>{
            if( a.date > b.date){
                return 1;
            }else if (a.date === b.date ){
                return 0;
            }else{
                return -1;
            }
        });
        return arrayChart;
    };
    const optionEchart = ():EChartOption => {
        const keys = arrayEchart().map( i => i.date.substr(5));
        const value = arrayEchart().map( i => i.total);
        let chartColor = [];
        if(type === '-'){
            chartColor.push('#1296db')
        }else{
            chartColor.push('red')
        };
        return {
            grid:{
                left: '10px',
                right: '10px'
            },             
            color: chartColor,
            xAxis: {
                type: 'category',
                data: keys,
                axisTick:{
                    alignWithLabel: true
                },
            },
            yAxis: {
                type: 'value',
                show:false
            },
            series: [{
                name:'账单',
                data: value,
                type: 'line',  
                symbolSize: 6,   
            }],   
            tooltip:{
                show: true,
                triggerOn: 'mousemove|click',
                position: 'top',
                formatter: '{c}',
                backgroundColor:'white',
            }
        }
    }; 
    return (
        <StatsEcharts2 option={optionEchart()} />
        
    )
};
export {StatsEcharts};