import { Layout } from "components/Layout";
import { SetNote } from "components/SetNote";
import { NumPad } from "components/money/NumPad";
import { TagsMoney } from "components/money/TagsMoney";
import { Types } from "components/Types";
import { addRecordId } from "help/addRecordId";
import { useRecords } from "hooks/useRecords";
import { useState } from "react";
import styled from "styled-components";
import RecordMoney from "type/RecordMoney";
import dayjs from 'dayjs'
import { Message } from "element-react";


const TagsWrapper = styled.div`
    overflow: auto;
    flex-grow: 2;   
    &::-webkit-scrollbar{ 
        display:none;         
    } 
`;
const defaultRecord:RecordMoney = {
    tagId: -1, 
    notes:'', 
    type: '-', 
    amount: '0',
    createAt: dayjs().format('YYYY-MM-DD')
};

const Money:React.FC = () => { 
    const [record, setRecord] = useState<RecordMoney>(defaultRecord);
    const {addRecord} = useRecords();
    const Change = (obj:Partial<RecordMoney>) =>{
        setRecord({
            ...record,
            ...obj
        })
    };
    const submitOk = () => {
        if(record.tagId === -1){
            Message({
                message:'请选择分类标签',
                duration: 1500,
                type:'warning'
            });
        }else{
            addRecord({
                ...record,
                id: addRecordId(),
                amount: parseFloat(record.amount)   
            })
            setRecord(defaultRecord)
        };       
    };
    return (
        <Layout>  
            <Types 
                value={record.type}
                onChange={(type) => Change({type})}/>
            <TagsWrapper>
                <TagsMoney 
                    type={record.type}
                    tagId={record.tagId}
                    onChange={(tagId)=> Change({tagId})}/>
            </TagsWrapper>
            <SetNote 
                spanNote = "日期"
                type="date"
                value = {record.createAt}
                onChange = {(createAt) => Change({createAt})} />
            <SetNote 
                spanNote = "备注"
                placeholder = "请添加备注"
                value = {record.notes}
                onChange = {(notes) => Change({notes})} />
            <NumPad 
                value={record.amount.toString()}
                onChange={(amount)=> Change({amount})}
                onOK={submitOk} />
        </Layout>
       
    )
};
export {Money};
