/* eslint-disable */
import { EChartOption } from "echarts/lib/echarts";
import { useEffect, useRef } from "react";
import * as echarts from 'echarts'; 
import styled from "styled-components";

type Props = {
    option:EChartOption
};
const Wrapper = styled.div`
    overflow-x:auto;
    flex-shrink:0;    
`;
const StatsEcharts2:React.FC<Props> = (props:Props) => {
    const {option} = props;
    const chart = useRef<echarts.ECharts>();
    const container = useRef<HTMLDivElement>(null);
    const wrapp = useRef<HTMLDivElement>(null);
    useEffect(()=>{
        chart.current = echarts.init(container.current!);
        setTimeout(()=>{
            if(!wrapp.current){
                return ;
            }else{
                wrapp.current.scrollLeft = wrapp.current.scrollWidth;
            }
        })
    }, []);
    useEffect(()=>{
        chart.current?.setOption(option);              
    },[option])
    return (
        <Wrapper ref={wrapp}>
        <div ref={container} style={{width:"400%",height:"240px"}}></div> 
        </Wrapper>      
    )
};
export {StatsEcharts2}