import { addTagId } from "help/addTagId";
import { useState } from "react";
import TagD from "type/TagD";
import { localEndTags } from "./localTags";
import { useSave } from "./useSave";
import { Message } from 'element-react';

const useTags = () =>{
    const [tags,setTags] = useState<TagD[]>(localEndTags());
  
    useSave(()=>{
        window.localStorage.setItem('tags', JSON.stringify(tags))
    }, [tags])
    
    //当前被选中的tag
    const currentTag = (id:number) => tags.filter(tag=>tag.id===id)[0];
    //currentTagIndex——当前标签的索引
    const currentTagIndex = (id:number) =>{  
        let result = -1;
        for(let i=0; i < tags.length; i++){
            if(tags[i].id === id){
                result = i;
                break;
            }
        };
        return result;
    };
    const getTagsContent = () =>{
        return tags.map((tag:TagD) => tag.tagContent)
    };

    // 编辑标签，不能更换类型
    const updateTag = (id:number ,obj:{name:string, tagContent:string}) =>{
        const index = currentTagIndex(id);
        if(index >= 0 ){
            if(obj.tagContent){
                const tagsContent = getTagsContent();
                const beforeTag = currentTag(id);
                if(tagsContent.indexOf(obj.tagContent) >= 0 && beforeTag.tagContent !== obj.tagContent ){
                    Message({
                        message:'标签名重复',
                        duration: 1500,
                        type: 'warning'
                    });
                }else{
                    setTags(tags.map(tag=>{
                        if(tag.id === id){
                            return {id:id, name:obj.name, tagContent: obj.tagContent, type: beforeTag.type}
                        }else{
                            return tag;
                        };
                    }));
                    Message({
                        message:'更改标签成功',
                        duration: 1500,
                        type:'success'
                    });
                };  
            }else{
                Message({
                    message:'请输入标签名',
                    duration: 1500,
                    type:'warning'
                });
            };                      
        }else{
            Message({
                message:'此标签不存在',
                duration: 1500,
                type:'error'
            });            
        };        
    };
    //新建标签
    const createTag = (obj:{type:'-'|'+',name:string, tagContent: string}) => {
        if(obj.tagContent===''){ 
            Message({
                message:'未输入标签名',
                duration: 1500,
                type: 'warning'
            });
            return;
        };
        const tagsContent = getTagsContent();
        if(tagsContent.indexOf(obj.tagContent) >= 0){           
            Message({
                message:'标签名重复',
                duration: 1500,
                type:'warning'
            });
        }else{
            const maxId = addTagId();
            setTags([
                ...tags,
                {id: maxId, name:obj.name, tagContent: obj.tagContent, type: obj.type}
            ]);
            Message({
                message:'创建标签成功',
                duration: 1500,
                type: 'success'
            });
        }
    };    
    return {tags, setTags, currentTag, updateTag, createTag};
};
export {useTags};
