import React from "react";
import { editOutput } from "./NumPadPart/editOutput";
import { NumPadWrapper } from "./NumPadPart/NumPadStyle";

type Props = {
    value:string;
    onChange: (val:string) => void;
    onOK:() => void;
};

const NumPad:React.FC<Props> = (props) =>{
    const output = props.value.toString();
    const SetOutput = ( op:string ) => {  //对结果进行封装               
        if(op.length >= 16){
            op = op.slice(0, 15);
        };
        if(op.length === 0 ){
            op = '0'
        };
        props.onChange(op);   
        return op;    
    };
    const Count = (e:React.MouseEvent) => {
    //React.MouseEvent是React自己封装的鼠标事件，可以传入参数HTMLElement，这样e.target就是点击的HTML元素
        const text = (e.target as HTMLButtonElement).textContent;
        if(!text){return;}
        if( text === '确定' ){  
            props.onOK()     
            return;
        }
        SetOutput(editOutput(text, output))
    }; 
    return (
        <NumPadWrapper> 
            <div className='output'>{output}</div> 
            <div className="buttons" onClick={Count}>
                <button>1</button>
                <button>2</button>
                <button>3</button>
                <button>删除</button>
                <button>4</button>
                <button>5</button>
                <button>6</button>
                <button>清零</button>
                <button>7</button>
                <button>8</button>
                <button>9</button>
                <button className='ok'>确定</button>
                <button className="zero">0</button>
                <button>.</button>
            </div>          
        </NumPadWrapper>
    )
};
export {NumPad};