//NumPad组件内的函数
const editOutput = (text:string, output = '0') => { 
    //output = '0'——指如果不输入就默认为 '0' 
    switch ( text ) {
        case '0':
        case '1':
        case '2':
        case '3':
        case '4':
        case '5':
        case '6':
        case '7':
        case '8':
        case '9':
            if(output==='0'){
                return text
            }else{
                return output + text
            };
        case '删除':
            if(output.length===1){
                return ''
            }else{
                return (output.slice(0, -1))
            };
        case '清零':
            return '';
        case '.':
            if(output.indexOf('.') >= 0){ return output }   
            return (output + '.');
        default:
            return '0';
    }
};

export {editOutput};
