//NumPad组件的样式
import styled from "styled-components";

const NumPadWrapper = styled.div`
    .output{       
        font-size: 28px;
        font-family: Consolas, monospace;
        padding: 4px 16px;
        text-align: right;
    }
    > .buttons{      
        padding: 6px;
        display: grid;
        grid-template-columns: 1fr 1fr 1fr 1fr;
        grid-template-rows: 44px 44px 44px 44px;
        gap: 4px 4px;
        > button{
                background-color: #d9d9d9;
                border: none;
            &.ok{
                grid-column: 4 / 5;
                grid-row: 3 / 5;
            }
            &.zero{
                grid-column: 1 / 3;
            }
        }
    }
`;
export {NumPadWrapper}