import dayjs from "dayjs";
import { RecordItem } from "type/RecordItem";


const groupList = (recordList: RecordItem[]):GroupsHashD[] => {
    if(recordList.length === 0){return [{ title:'目前没有记录', items:[] ,total: 0}]};

    // 将记录按日期从近到远排序得到 newRecordList，这里使用了 dayjs库的 valueOf
    const newRecordList = recordList.sort((a, b) => 
            dayjs(b.createAt).valueOf() - dayjs(a.createAt).valueOf());

    // 初始值——最近的一次记录表
    const result: GroupsHashD[] = [
        {title: newRecordList[0].createAt,
        total: newRecordList[0].amount,
        items:[newRecordList[0]]}
    ];
    // 已经将第一个最近的记录设为初始值，因此这里设let i = 1，从第二位开始
    for (let i = 1; i < newRecordList.length; i++) {
        const current = newRecordList[i];
        const lastGroup = result[result.length - 1];
        if(dayjs(lastGroup.title).isSame(dayjs(current.createAt), 'day')){
            lastGroup.items.push(current);
            lastGroup.total += current.amount;
        }else{
            result.push({
                title: current.createAt,
                total: current.amount,
                items: [current]
            })
        };            
    };
    return result;
};
export {groupList};