import dayjs from "dayjs";

const shortDate = (time:string) => {
    if(time==='目前没有记录'){return '目前没有记录'}
    const time2 = dayjs(time);
    const now = dayjs();
    if(time2.isSame(now,'day')){
        return '今天'
    }else if(time2.isSame(now.subtract(1, 'day'), 'day')){
        return '昨天'
    }else if(time2.isSame(now.subtract(2, 'day'), 'day')){
        return '前天'
    }else if(time2.isSame(now, 'year')){
        return time2.format('M月D日') 
    }else{
        return time2.format('YYYY年M月D日');
    }
};
export {shortDate};