import styled from "styled-components";

const Groups = styled.ol`
    color: #333;    
    padding: 10px 12px;       
    .group{     
        margin-bottom: 10px;
        h3{
            display: flex;
            justify-content: space-between;
            font-size: 16px;  
            padding: 6px;          
        }
        .items{
            .item{
                background-color: white;                
                border: 1px solid #d9d9d9;
                min-height: 52px;
                border-radius: 8px;   
                display: flex;                
                align-items: center;
                margin-bottom: 6px;                
                font-size: 16px;
                .tagContent{
                    overflow:hidden; 
                    white-space:nowrap; 
                    text-overflow:ellipsis;
                    padding: 2px 10px; 
                    flex-grow: 1;    
                    .notes{
                        font-size: 13px;
                        color: gray;
                    }               
                }
                > .label-icon{
                    margin-left: 12px;
                    flex-shrink: 0;  
                                    
                }
                .amount{
                    padding: 0 10px; 
                }
            }
        }
    }
`;
export {Groups};