import { Back } from "components/Back";
import { Button } from "components/Button";
import { IconList } from "components/IconList";
import { Input } from "components/Input";
import { useTags } from "hooks/useTags";
import { useState } from "react";
import { useHistory, useParams } from "react-router-dom";
import styled from "styled-components";
import TagD from "type/TagD";

const Wrapper = styled.div`
    input{
        margin-top: 10px;
        font-size: 22px;
        background-color: #f5f5f5;
        padding: 4px 16px;
        text-align: left;
        width: 100%;
        border: none;
    }
    .tagArea{
        padding-top: 30px;
    }
`;
type Params = {
    newTagType: '-' | '+'
};
const NewTag:React.FC = (props) => {
    const newType = useParams<Params>().newTagType;
    const history = useHistory();
    const {createTag} = useTags();
    const [newTag, setNewTag] = useState<TagD>({
        id: -1,
        name:'',
        tagContent:'',
        type: newType
    });
    const addNewTag = () => {
        createTag(
            { type: newTag.type, name: newTag.name, tagContent: newTag.tagContent }
        );
        history.goBack();
    };

    return (
        <Wrapper>
            <Back headText="添加分类"/>
            <Input value={newTag.tagContent} onChange={(text)=>setNewTag({
                ...newTag,
                tagContent:text
            })} />
            <div className="tagArea">
                <IconList 
                    currenTag={newTag} 
                    type={newTag.type} 
                    onChange={name=>setNewTag({
                        ...newTag,
                        name: name
                    })} />
            </div>
            <Button butText="保存"
                changeOk={addNewTag}>
            </Button>
        </Wrapper>
    )
};
export {NewTag};
