import { Icon } from "components/Icon";
import { useTags } from "hooks/useTags";
import styled from "styled-components";

const Wrapper = styled.div`
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    padding-top: 10px;
    .cost.selected{
        .label-icon{
            color: #1296db;
        }
    }
    .earn.selected{
        .label-icon{
            color: red;
        }
    }
    .item{
        display: flex;
        flex-direction: column;
        align-items: center;
        padding-bottom: 10px;
        span{
            max-width: 48px;  
            overflow:hidden; 
            white-space:nowrap; 
            text-overflow:ellipsis;
            font-size: 16px;
        }
        .label-icon{
            margin-bottom: 8px;
        }
    }
`;
type Props = {
    type:'-'|'+';
    tagId:number;
    onChange:(id:number) => void;
}

const TagsMoney:React.FC<Props>= (props) => {
    const tags = useTags().tags.filter((r)=>r.type===props.type);
    const currenTag = props.tagId;
    const ToggleTagId = (tagID:number) =>{
        props.onChange(tagID)
    };   
    
    return (   
        <Wrapper>
            {tags.map(tag=>
                <div key={tag.id}      
                    className ={[
                        tag.id === currenTag ? 'selected' : '',
                        'item',
                        tag.type==='-' ? 'cost' : 'earn',
                    ].join(' ')}              
                    onClick={()=>{
                        ToggleTagId(tag.id)}
                    } 
                    //需要使用箭头函数，如果直接ToggleTagId(tag.id)，那这个函数会被立刻调用
                >
                    <div className='label-icon'>
                    <Icon name={tag.name} />
                    </div>
                    <span>{tag.tagContent}</span>
                </div>
            )}
        </Wrapper>
    )
};
export {TagsMoney};
